$maxWidth: 330px;
$radius: 5px;

.Calendar-wrap {
    border: 1px solid #f0f0f0;
    border-radius: $radius;
    overflow: hidden;

    table.Calendar {
        font: 400 14px Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
        background-color: #fff;
        // box-shadow: 0px 0px 1px #494949;
        max-width: $maxWidth;
        // border-radius: $radius;
        overflow: hidden;
        // border-spacing: 1px;
        border-collapse: collapse;

        td,
        th {
            text-align: center;
            padding: 6px;
            font-size: 14px;
            width: $maxWidth / 5;
            line-height: 26px;
            transition: all 0.2s ease-out;
            user-select: none;
        }

        th.prevY,
        th.prevM,
        th.nextY,
        th.nextM {
            cursor: pointer;

            &:hover {
                background-color: #b8cde4;
            }

            &:active {
                background-color: #3178c9;
            }
        }

        .Calendar-head .cur {
            font-weight: bold;
        }

        td.cur {
            color: #000;
        }

        td.today {
            background-color: #b8cde4;
            color: #3178c9;
            font-weight: bold;
        }

        td {
            color: #c2c2c2;
            cursor: pointer;

            &:hover {
                background-color: #b8cde4;
                color: #000;
            }

            &:active {
                background-color: #3178c9;
                color: #fff;
            }
        }

        tfoot {
            background-color: #3178c9;

            tr {
                td {
                    color: #fff;
                }
            }

        }
    }
}
